const chooseLevelWorkout = () => {
  const select = document.querySelector('[data-select="directed"]');
  const selectPrint = document.querySelector('[data-print="directed"]');

  if (selectPrint) {
    select.onchange = function () {
      selectPrint.innerHTML = this.value;
    };
  }

  const printChooseInput = (inputsName, blockPrint) => {
    const inputs = document.querySelectorAll(inputsName);
    const block = document.querySelector(blockPrint);

    inputs.forEach((item) => {
      item.onchange = function () {
        block.innerHTML = item.value;
      };
    });
  };

  printChooseInput('[data-name="level"]', '[data-print="level"]');
  printChooseInput('[data-name="amountEx"]', '[data-print="amount-video"]');

};

export {chooseLevelWorkout};
